.App {
	text-align: center;
	margin: 0 !important;
	padding: 0 !important;
	width: 100vw;
	height: 100vh;
}


.FourOhFour
{
  background-color:#141019;
  background: radial-gradient(at 50% -20%, #908392, #0d060e) fixed;
}


#handboy
{
   -webkit-animation: swing ease-in-out 1.3s infinite alternate;
           animation: swing ease-in-out 1.3s infinite alternate;
    -webkit-transform-origin: 98% 98%;
            transform-origin: 98% 98%;
    transform-box: fill-box;
    
}


#girllight
{
   -webkit-animation: swing ease-in-out 1.3s infinite alternate;
           animation: swing ease-in-out 1.3s infinite alternate;
    -webkit-transform-origin: 0% 97%;
            transform-origin: 0% 97%;
    transform-box: fill-box;
}

#hairgirl
{
    -webkit-animation: swinghair ease-in-out 1.3s infinite alternate;
            animation: swinghair ease-in-out 1.3s infinite alternate;
   -webkit-transform-origin: 60% 0%;
           transform-origin: 60% 0%;
    transform-box: fill-box;
  
}

#zero
{
  -webkit-transform-origin:bottom;
          transform-origin:bottom;
  transform-box:fill-box;
  
}

/*************swing************/
@-webkit-keyframes swing {
    0% { -webkit-transform: rotate(10deg); transform: rotate(10deg); }
    100% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
}
@keyframes swing {
    0% { -webkit-transform: rotate(10deg); transform: rotate(10deg); }
    100% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
}


/*************swing hair************/
@-webkit-keyframes swinghair {
    0% { -webkit-transform: rotate(6deg); transform: rotate(6deg); }
    100% { -webkit-transform: rotate(-6deg); transform: rotate(-6deg); }
}
@keyframes swinghair {
    0% { -webkit-transform: rotate(6deg); transform: rotate(6deg); }
    100% { -webkit-transform: rotate(-6deg); transform: rotate(-6deg); }
}
.LandingPage {
	padding: 1rem 0 0 0;
	width: 100%;
	margin: 0;
	text-align: center;
	overflow-x: hidden;
}
.LandingPage-hero {
	padding: 0;
	position: relative;
	background-color: #485461;
	background-image: linear-gradient(315deg, #485461 0%, #28313b 74%);
}
.LandingPage-hero-img {
	width: 100vw;
	height: 28rem;
	background-image: url(/static/media/patrick-tomasso-GXXYkSwndP4-unsplash.0812674c.jpg);
	opacity: 0.5;
	background-size: cover;
	position: relative;
	background-position: 25% 50%;
	margin: 0;
}

.LandingPage-title-text {
	position: absolute;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	width: 50%;
	height: 10rem;
	top: 50%;
	left: 50%;
	color: white;
	padding-top: 1rem;
	border: 10px double white;
	font-family: 'Montserrat', sans-serif;
	font-weight: 800;
	text-shadow: 4px 4px #443e3e;
}

.LandingPage-offer-text {
	font-family: "Montserrat", sans-serif;
	padding-top: 4rem;
	padding-bottom: 2rem;
	font-size: 3rem;
}
.LandingPage-modal-login {
	width: 50%;
	height: 50%;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.LandingPage-modal-register {
	width: 50%;
	height: 50%;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -80%);
	        transform: translate(-50%, -80%);
}

.LandingPage-row {
	margin: auto;
	padding-top: 2rem;
}

.LandingPage-bottom {
	padding-bottom: 5rem;
}

.LandingPage-social {
	padding-top: 1rem;
}

.LandingPage-text-container {
	position: relative;
}

.LandingPage-text {
	font-family: "Montserrat", sans-serif;
	font-weight: 600;
	font-size: 1.5rem;
}

.LandingPage-row img {
	margin-right: 2rem;
	width: 100%;
}

.LandingPage-buttons {
	font-family: "Montserrat", sans-serif;
}

#LandingPage-social {
	width: 2rem;
	height: auto;
	margin: auto;
	margin-top: 10px;
}

#LandingPage-social:hover {
	cursor: pointer;
}

@media screen and (min-width: 768px) {
	.LandingPage-text {
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);
		width: 100%;
	}
}

@media screen and (max-width: 768px) {
	.LandingPage-img {
		padding-top: 3rem;
	}

	.LandingPage-img img {
		width: 100%;
		height: 100%;
		object-fit: contain;
	}

	.LandingPage-text {
		padding-top: 2rem;
	}
	.LandingPage-title-text {
		width: 95%;
		height: 15rem;
	}
}



.Dashboard-main {
	padding-top: 5rem;
}

.Dashboard-buttons-container {
	position: relative;
}

.Dashboard-buttons {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	width: 100%;
}

@media screen and (max-width: 769px) {
	.Dashboard-buttons-container {
		margin-bottom: 2rem;
	}
	.Dashboard-buttons {
		position: inherit;
		display: flex;
	}
	.Dashboard-chart {
		margin-bottom: 2rem;
	}
}


